html, body, #root {
    font: 0.9rem sans-serif;
    background-color: #C8D2D1;    
    height: 100%;
    margin: 0;
}

#idBtn {    

    


    outline: none;
    font-size: 24px;
    padding: 20px 7%;
    border-radius: 20px;
    margin-bottom: 5%;
    background: #EE9B01;

}


.main-container {
    background-size: cover;
    background-position: center;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}


.search {
    outline: none;
    padding: 20px 7%;
    border-radius: 20px;
    border: none;
    margin-bottom: 5%;
    background: rgba(250, 250, 250, 0.85);
}


#response {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 40px 8%;
    border-radius: 20px;
    background: rgba(250, 250, 250, 0.85);
    box-shadow: 10px 10px 5px 0px rgba(15, 15, 15, 0.404);
}
